@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
.page-main {
  display: flex;
  flex-direction: column;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  background-color: #ffffff;
  outline: 2px solid rgba(255, 255, 0, 0.5);
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 320px;
  margin: 0 auto;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1300px) {
  .container {
    width: 1300px;
  }
}
.button {
  display: block;
  width: 280px;
  height: 40px;
  padding: 11px 0;
  box-sizing: border-box;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #68b738;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button {
    width: 250px;
    padding: 13px 0;
    font-size: 16px;
    line-height: 20px;
  }
}
@media (min-width: 1300px) {
  .button {
    width: 170px;
  }
}
.page-header {
  display: flex;
  flex-wrap: wrap;
}
.page-header__wrapper {
  display: flex;
  padding: 0 20px;
  border-bottom: 1px solid #e6e6e6;
}
.page-header__logo {
  display: block;
  margin: 11px 0 15px;
  width: 191px;
  height: 39px;
}
.page-header__logo:not(.page-header__logo--active):hover,
.page-header__logo:not(.page-header__logo--active):focus {
  opacity: 0.8;
}
.page-header__logo:not(.page-header__logo--active):active {
  opacity: 0.6;
}
.page-header__toggle {
  display: block;
  width: 25px;
  height: 19px;
  padding: 0;
  margin: 23px 0 23px 65px;
  background-color: white;
  background-image: url("../img/icon-mobile-menu.svg");
  background-repeat: no-repeat;
  border: none;
  outline: none;
}
.page-header__toggle--no-js {
  display: none;
}
.page-header__toggle--show {
  width: 18px;
  height: 18px;
  margin-left: 69px;
  background-image: url("../img/icon-close.svg");
}
@media (min-width: 768px) {
  .page-header {
    min-height: 141px;
  }
  .page-header__wrapper {
    width: auto;
    padding: 0;
    border: none;
  }
  .page-header__logo img {
    width: 174px;
    height: 59px;
    margin: 12px 0 0 30px;
  }
  .page-header__toggle {
    display: none;
  }
}
@media (min-width: 1300px) {
  .page-header {
    min-height: 142px;
  }
  .page-header__logo img {
    width: 203px;
    height: 60px;
    margin: 43px 0 0 39px;
    background-size: 202px 60px;
  }
}
.main-nav {
  display: none;
  margin-left: auto;
}
.main-nav--no-js {
  display: block;
}
.main-nav--show {
  display: block;
}
@media (min-width: 768px) {
  .main-nav {
    display: block;
  }
}
.functional {
  position: relative;
  min-height: 480px;
  padding: 0 20px;
