}
@media (min-width: 768px) {
  .functional {
    min-height: 800px;
    padding: 0 120px;
    margin-bottom: 10px;
    background-image: url("../img/index-can-tablet@1x.jpg");
    background-position: 30px 200px;
  }
  .functional::after {
    content: none;
  }
  .functional__title {
    margin: 0;
    margin-bottom: 40px;
    font-size: 60px;
    line-height: 60px;
    color: #111111;
    text-align: left;
