@media (min-width: 1300px) {
  .functional {
    position: static;
    min-height: 555px;
    background-image: url("../img/index-can-desktop@1x.png");
    background-position: 490px 0;
  }
  .functional::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 0;
    min-height: 696px;
    width: 50%;
    background-color: #64a93c;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-size: 720px 696px;
    background-repeat: no-repeat;
    z-index: -1;
  }
  .functional__title {
    width: 420px;
    margin: 80px 0 40px;
  }
  .functional__button {
    margin-top: 53px;
  }
}
@media (min-width: 1300px) and (min-resolution: 144dpi), (min-width: 1300px) and (min-resolution: 1.5dppx) {
  .functional {
    background-image: url("../img/index-can-desktop@2x.png");
    background-size: 552px 499px;
  }
}
@media (min-width: 1300px) and (min-resolution: 144dpi), (min-width: 1300px) and (min-resolution: 1.5dppx) {
  .functional::after {
    background-image: url("../img/index-background-desktop@2x.jpg");
  }
}
.categories {
  display: flex;
  flex-direction: column;
}
.categories__weight {
  position: relative;
  margin: 20px 20px 0;
  padding: 20px;
  box-sizing: border-box;
  background-color: #f2f2f2;
}
.categories__title {
  position: relative;
  margin: 13px 0 29px 72px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;
  color: #111111;
}
.categories__title::before {
  content: "";
  position: absolute;
  top: -7px;
  left: -70px;
  display: block;
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;
}
.categories__title::after {
  content: "";
  position: absolute;
  display: block;
  background-repeat: no-repeat;
}
.categories__title--slim::after {
  top: -8px;
  left: -62px;
  width: 35px;
  height: 49px;
  background-image: url("../img/icon-cat-slim.svg");
}
.categories__title--pro::after {
  top: 4px;
  left: -78px;
  width: 67px;
  height: 28px;
  background-image: url("../img/icon-cat-pro.svg");
}
.categories__description {
  padding-bottom: 23px;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  border-bottom: 1px solid #d9d9d9;
}
.categories__button {
  position: relative;
  display: block;
  margin-top: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  color: #111111;
  text-decoration: none;
  text-transform: uppercase;
}
.categories__button::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 96px;
  display: block;
  width: 25px;
  height: 11px;
  background-image: url("../img/icon-right-arrow-long.svg");
  background-repeat: no-repeat;
}
.categories__button--pro::after {
  left: 94px;
}
.categories__button:hover::after,
.categories__button:focus::after {
  width: 33px;
  background-image: url("../img/icon-right-arrow-verylong.svg");
}
.categories__button:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .categories__weight {
    margin: 0 30px 30px;
    padding: 50px;
  }
  .categories__title {
    margin: 25px 0 5px 5px;
    font-size: 36px;
  }
  .categories__title::before {
    top: 15px;
    left: 392px;
    width: 200px;
    height: 200px;
  }
  .categories__title--slim::after {
    top: 5px;
    left: 421px;
    width: 141px;
    height: 194px;
  }
  .categories__title--pro::after {
    top: 60px;
    left: 358px;
    width: 268px;
    height: 113px;
  }
  .categories__description {
    margin: 0;
    padding: 20px 0 57px 2px;
    font-size: 16px;
    line-height: 24px;
    border-bottom: none;
  }
  .categories__description--slim {
    width: 380px;
  }
  .categories__description--pro {
    width: 340px;
  }
  .categories__button {
    margin-top: 0;
    margin-bottom: 12px;
    margin-left: 2px;
    font-size: 20px;
    line-height: 20px;
  }
  .categories__button::after {
    top: 5px;
    left: 132px;
  }
  .categories__button--pro::after {
    left: 127px;
  }
}
@media (min-width: 1300px) {
  .categories {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 70px 40px 0;
    box-sizing: border-box;
  }
