.product--show-more {
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid #d9d9d9;
}
.product__title {
  width: 75px;
  margin: 22px 0 12px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #111111;
  text-transform: uppercase;
}
.product__title--show-more {
  width: 280px;
  margin: 23px 0 7px;
  text-align: center;
}
.product__image--chicken-small {
  width: 140px;
  height: 144px;
}
.product__image--show-more {
  width: 30px;
  height: 30px;
  padding-top: 28px;
}
.product__image--show-more polygon {
  fill: rgba(34, 34, 34, 0.2);
}
.product__feature {
  width: 140px;
  margin: 0;
  padding: 0;
  border-collapse: collapse;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}
.product__feature td {
  padding: 0;
}
.product__feature--show-more {
  width: 280px;
  text-align: center;
}
.product__feature td:last-child {
  text-align: right;
}
.product__button {
  padding: 10px 0;
  font-size: 16px;
  line-height: 20px;
}
.product__button--show-more {
  margin-top: 20px;
  color: #444444;
  background-color: #f2f2f2;
}
.product__button--show-more:hover,
.product__button--show-more:focus {
  background-color: #ebebeb;
}
.product__button--show-more:active {
  color: rgba(68, 68, 68, 0.3);
}
@media (min-width: 768px) {
  .product {
    flex-direction: column;
    padding-bottom: 37px;
    align-items: center;
    border-top: none;
  }
  .product--show-more {
    padding-top: 25px;
    background-color: #ffffff;
    border: 1px solid #cdcdcd;
  }
  .product__title {
    width: 135px;
    margin: 165px auto 18px;
    font-size: 20px;
    text-align: center;
  }
  .product__title--show-more {
    margin: 50px 0 28px;
  }
  .product__image {
    position: absolute;
    left: 0;
    width: 324px;
  }
  .product__image--chicken-small,
  .product__image--fish-small,
  .product__image--buckwheat-small,
  .product__image--rice-small {
    top: -45px;
    height: 172px;
  }
  .product__image--chicken-big,
  .product__image--fish-big,
  .product__image--buckwheat-big {
    top: -81px;
    height: 208px;
  }
  .product__image--show-more {
    width: 61px;
    height: 61px;
  }
  .product__feature {
    width: 250px;
    margin-bottom: 33px;
    font-size: 16px;
  }
  .product__feature td {
    padding: 8px 0;
    border-bottom: 1px solid #d9d9d9;
  }
  .product__feature--show-more {
    width: 170px;
    margin-bottom: 0;
    line-height: 18px;
  }
  .product__button--show-more {
    margin-top: 85px;
  }
}
@media (min-width: 1300px) {
  .product__title {
    margin-bottom: 20px;
  }
  .product__title--show-more {
    margin-bottom: 25px;
  }
  .product__image {
    width: 245px;
  }
  .product__feature {
    width: 168px;
  }
  .product__feature td {
    padding: 7px 0;
  }
}
